import React, { Component, } from 'react';
import { Row, Col, Form, Select, Input, } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';

const { Option } = Select;



@Form.create({ name: 'formScholarshipApproval' })


class SearchBar extends Component {
  state = {
  };

  handleReset = () => {
    this.props.form.resetFields();
  }

  handleSearch = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  }


  render() {
    const { getFieldDecorator } = this.props.form;

    return (
      <Form
        className="ant-advanced-search-form"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>
          <Col span={8} >
            <Form.Item label='H'>
              {getFieldDecorator('system1', {
              })(
                <Select>
                  <Option value="1">i hc chnh quy</Option>
                  <Option value="2">Cao ng chnh quy</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Khoa'>
              {getFieldDecorator('system2', {
              })(
                <Select>
                  <Option value="1">Cng ngh thc phm</Option>
                  <Option value="2">K ton</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Kha'>
              {getFieldDecorator('system3', {
              })(
                <Select>
                  <Option value="1">Kha 1</Option>
                  <Option value="2">Kha 2</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='CN'>
              {getFieldDecorator('system4', {
              })(
                <Select>
                  <Option value="1">K ton kim ton</Option>
                  <Option value="2">K ton kho</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Hc k'>
              {getFieldDecorator('system5', {
              })(
                <Select>
                  <Option value="1">Hc k 1</Option>
                  <Option value="2">Hc k 2</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Nm hc'>
              {getFieldDecorator('system6', {
              })(
                <Select>
                  <Option value="1">2017-2018</Option>
                  <Option value="2">2018-2019</Option>
                  <Option value="3">2019-2020</Option>
                  <Option value="4">2020-2021</Option>
                </Select>
              )
              }
            </Form.Item>
          </Col>

          <Col span={8} >
            <Form.Item label='M sinh vin'>
              {getFieldDecorator('system11', {
              })(
                <Input />)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='H tn'>
              {getFieldDecorator('system12', {
              })(
                <Input />)
              }
            </Form.Item>
          </Col>
          <Col span={8} >
            <Form.Item label='Loi quyt nh'>
              {getFieldDecorator('system13', {
              })(
                <Select >
                  <Option value="1">Hc tip</Option>
                  <Option value="2">Thi hc</Option>
                </Select>)
              }
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar



