import React, { Component, Fragment } from 'react';
// import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Table, Popconfirm } from 'antd';
import { DropoutList } from 'dump/data';


const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.name === 'Disabled User', // Column configuration not to be checked
    name: record.name,
  }),
};


const columns = [
  // {
  //   title: 'Chn',
  //   dataIndex: 'nameRating',
  //   render: () =>
  //     DropoutList.length >= 1 ? (
  //       <Fragment>
  //         <Checkbox />
  //       </Fragment>
  //     ) : null,
  // },
  {
    title: 'S Q',
    dataIndex: 'number',
    key: 'number',
  },
  {
    title: 'Ngy Q',
    dataIndex: 'date',
    key: 'date',
  },
  {
    title: 'Ni dung',
    dataIndex: 'content',
    key: 'content',
  },
  {
    title: 'M SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'H v tn',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lp c',
    dataIndex: 'oldClass',
    key: 'oldClass',
  },
  {
    title: 'Lp mi',
    dataIndex: 'newClass',
    key: 'newClass',
  },
  {
    title: 'Thao tc',
    dataIndex: 'operation',
    render: (text, record) =>
      DropoutList.length >= 1 ? (
        <Fragment>
          {/* <BtnEdit href={`#DropoutList/form/edit/${record.codeRating}`} /> */}
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class TableDropoutList extends Component {
  state = {};

  render() {

    return (
      <Table rowSelection={rowSelection} bordered dataSource={DropoutList} columns={columns} />
    );
  }
}

export default TableDropoutList;



