import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col } from 'antd';
import TableDropoutList from './TableDropoutList';
import SearchBar from './SearchBar';
import './style.scss';

class DropoutListlPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Danh sch thi, ngng hc">
        <SearchBar />
        <Row>
          <Col span={24}>
            <div className="btn-group">
              <BtnAddNew title="Nhp Q" href="#/studentProfile/DropoutList/from" />
              <BtnAddNew title="Nhp xt hc tip" href="#/studentProfile/DropoutList/from" />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableDropoutList />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DropoutListlPage />
  </Page>
);



